import { classify } from 'src/util'

import ScrollPane from 'components/ScrollPane.vue'
import ActionHeader from 'components/ActionHeader.vue'

const ENTRIES = [
  'beforeCreate',
  'created',
  'beforeMount',
  'mountRender',
  'mounted',
  'beforeUpdate',
  'updateRender',
  'updated',
  'beforeDestroyed',
  'destroyed'
]

const COLUMNS = [
  'type',
  'count',
  'total time',
  'average time'
]

export default {
  components: {
    ScrollPane,
    ActionHeader
  },

  props: {
    entry: {
      type: Object,
      default: null
    }
  },

  computed: {
    entries () {
      return ENTRIES.map(type => ({
        id: type,
        ...this.entry.hooks[type] || { totalTime: 0, count: 0 }
      }))
    },

    componentName () {
      return (this.$shared.classifyComponents ? classify(this.entry.id) : this.entry.id) || 'Anonymous Component'
    },

    highDensity () {
      const pref = this.$shared.displayDensity
      return (pref === 'auto' && this.entries.length > 8) || pref === 'high'
    }
  },

  created () {
    this.columns = COLUMNS
  }
}
