import { mapState, mapMutations, mapGetters } from 'vuex'

export default {
  name: 'RoutesTreeItem',
  props: {
    routeId: {
      type: [String, Number],
      required: true
    },
    route: {
      type: Object,
      required: true
    },
    depth: {
      type: Number,
      required: true
    }
  },
  data () {
    return {
      expanded: false
    }
  },
  computed: {
    ...mapState('routes', [
      'inspectedIndex'
    ]),
    ...mapGetters('routes', [
      'activeRoute'
    ]),
    selected () {
      return this.inspectedIndex === this.routeId
    },
    isActive () {
      return this.activeRoute && this.activeRoute.path === this.route.path
    }
  },
  methods: {
    ...mapMutations('routes', {
      inspect: 'INSPECT'
    }),
    toggleExpand () {
      this.expanded = !this.expanded
    }
  }
}
